:root {
  --default-duration: 0.6s;
}

@-webkit-keyframes arrowAnimationType-1 {
  0% {
    background-position: 0 0;
  }

  50% {
    background-position: 18px -18px;
  }

  51% {
    background-position: -18px 18px;
  }

  100% {
    background-position: 0 0;
  }
}

@keyframes arrowAnimationType-1 {
  0% {
    background-position: 0 0;
  }

  50% {
    background-position: 18px -18px;
  }

  51% {
    background-position: -18px 18px;
  }

  100% {
    background-position: 0 0;
  }
}

@-webkit-keyframes arrowAnimationType-2 {
  0% {
    background-position: 0 0;
  }

  50% {
    background-position: 32px -32px;
  }

  51% {
    background-position: -32px 32px;
  }

  100% {
    background-position: 0 0;
  }
}

@keyframes arrowAnimationType-2 {
  0% {
    background-position: 0 0;
  }

  50% {
    background-position: 32px -32px;
  }

  51% {
    background-position: -32px 32px;
  }

  100% {
    background-position: 0 0;
  }
}

@-webkit-keyframes subscribeAnimation {
  0% {
    background-position-y: 0;
  }

  50% {
    background-position-y: -30px;
  }

  51% {
    background-position-y: 30px;
  }

  100% {
    background-position-y: 0;
  }
}

@keyframes subscribeAnimation {
  0% {
    background-position-y: 0;
  }

  50% {
    background-position-y: -30px;
  }

  51% {
    background-position-y: 30px;
  }

  100% {
    background-position-y: 0;
  }
}

@-webkit-keyframes loadingAnimation {
  0% {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
    transform: translate(-50%, -50%) rotate(0deg);
  }

  100% {
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
    transform: translate(-50%, -50%) rotate(360deg);
  }
}

@keyframes loadingAnimation {
  0% {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
    transform: translate(-50%, -50%) rotate(0deg);
  }

  100% {
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
    transform: translate(-50%, -50%) rotate(360deg);
  }
}

