    margin-left: calc(-20px + -26 * ((100vw - 320px) / 1120));
  }

  .quote__controls {
    margin-right: calc(-20px + -26 * ((100vw - 320px) / 1120));
  }
}

@media screen and (max-width: 768px) and (min-width: 768px) {
  :root {
    --products-card-background-position: calc(708px + -386 * ((100vw - 768px) / 672));
  }
}

@media (max-width: 767px) and (min-width: 768px) {
  .quote__wrapper {
    max-width: calc(730px + -30 * ((100vw - 768px) / 672));
  }
}